* {
  margin: 0;
  padding: 0;
}
ul,
li {
  list-style: none;
}
img {
  display: block;
}
.wh100 {
  height: 100%;
  width: 100%;
}
.flex_center {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.app_main {
  height: 100%;
  width: 100%;
}
header {
  padding: .7rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: relative;
}
header .run_data {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 100%;
  width: 100%;
  flex: 3;
  padding: 0 1rem 0 1.7rem;
}
header .info li {
  padding: .7rem 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: column;
}
header .info p {
  color: #e23f3a;
  font-size: 1.3rem;
}
header .user_info {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: column;
  flex: 2;
}
header .rank {
  padding-bottom: .4rem;
}
header .user_name {
  padding-left: .4rem;
}
header .user_name_and_avatar {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
header .rank_logo {
  height: 6rem;
  width: 5rem;
}
header .avatar {
  width: 3.3rem;
  height: 3.3rem;
  border-radius: 50%;
}
.progress {
  border-top: .1rem solid #ccc;
  border-bottom: .1rem solid #ccc;
  width: 100%;
  padding: 1.5rem 1rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  justify-content: flex-start;
}
.progress .left {
  padding-right: .7rem;
}
.progress p {
  text-align: center;
  padding-bottom: .7rem;
}
.progress section {
  position: relative;
}
.progress section div {
  height: 10px;
  width: 16rem;
  background-color: #eed0d3;
  border-radius: 10px;
}
.progress .box2 {
  background-color: #e23f3a;
  position: absolute;
  bottom: 0;
}
.progress .num {
  color: #e23f3a;
}
.location {
  position: relative;
  width: 100%;
  height: 42rem;
}
.location img {
  height: 100%;
  width: 100%;
}
.location p {
  position: absolute;
  top: 0;
  color: #FFF;
  width: 100%;
  padding: 1rem;
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
}
.location p span:first-child {
  display: block;
  padding-bottom: .4rem;
}
.location .map_mask {
  background-color: #000;
}
.location .map_mask img {
  opacity: 0.6;
}
footer {
  background-color: #f5f5f5;
  width: 100%;
  height: 6.5rem;
  overflow: hidden;
  position: fixed;
  bottom: 0;
  border-top: .1rem solid #ccc;
}
footer p,
footer span {
  color: #fff;
}
footer .challenge {
  text-align: center;
  padding: 1rem 0 .3rem 0;
  /*font-weight: bold;*/
  color: #e23f3a;
}
footer .runmate .logo {
  height: 3rem;
  width: auto;
  margin-left: 1rem;
}
footer p.runmate {
  font-size: .7rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 1.5rem;
}
footer a {
  text-decoration: none;
  display: block;
  background-color: #e23f3a;
  color: #fff;
  padding: .3rem .7rem;
  border-radius: .3rem;
  margin-right: 3rem;
  /*box-shadow: 2px 2px 2px #ff4949;*/
}
.map_hide {
  height: 25rem;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.map_hide img {
  height: 4rem;
}
.map_hide p {
  color: #000;
  text-align: center;
  padding: 1rem 0;
}
.logo_intro {
  display: flex;
  align-items: center;
  justify-content: center;
}
.intro {
  padding-left: .3rem;
  color: #000;
}
@keyframes rotate {
  0%,
  100% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.not_login,
.err {
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.err img {
  height: 3rem;
}
.not_login span,
.err span {
  display: block;
  padding: 1rem;
}
.login_logo {
  width: 3rem;
  animation: rotate 2s linear infinite;
}
.wobuxiangzaijiale {
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 1rem;
}
.wobuxiangzaijiale img {
  height: 2rem;
  width: auto;
  padding-right: 1rem;
}



